import { ProcessStepElement, ProcessStepElementBase } from '@jumoo/processing';
import { customElement, html } from '@umbraco-cms/backoffice/external/lit';
import { uSyncExporterSteps } from '../constants';

@customElement(uSyncExporterSteps.import.result)
export class uSyncImportResultStep
	extends ProcessStepElementBase
	implements ProcessStepElement
{
	constructor() {
		super();
		console.log('uSyncImportResultStep');
	}

	render() {
		console.log('uSyncImportResultStep.render');
		var actions = this.results?.results['actions'] as any[];
		var count = actions?.length ?? 0;

		return html`<div>
			<uui-box>
				<div slot="header">
					<em>Imported Sync-pack contains ${count} items</em>
				</div>
				<usync-results .results=${actions}></usync-results>
			</uui-box>
		</div>`;
	}
}

export default uSyncImportResultStep;
